$breack_points_up: (
  "medium": "375px",
  "large": "1200px",
);
$breack_points_down: (
  "small": "376px",
  "medium": "1201px",
);

@mixin breack-point-up($size) {
  @media (min-width: map-get($breack_points_up,$size)) {
    @content;
  }
}
@mixin breack-point-down($size) {
  @media (max-width: map-get($breack_points_down,$size)) {
    @content;
  }
}
