.main_cont {
  @include breack-point-down(small) {
    overflow: hidden;
  }
  @include breack-point-up(medium) {
    padding: 0px 212px;
  }
  position: relative;
  @include breack-point-up(medium) {
    display: flex;
    justify-content: space-between;
    margin-top: 90px;
  }
}
.picture_cont {
  position: relative;
  margin-bottom: 24px;
  @include breack-point-up(medium) {
    margin-right: 90px;
  }
}
.banner {
  max-width: 100vw;
  @include breack-point-up(medium) {
    width: 445px;
    border-radius: 10px;
    z-index: 4;
    cursor: pointer;
  }
}
.image_cont {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 40vw 20px 0px 20px;
}
.previous {
  height: 40px;
  width: 40px;
  padding: 13px;
  background-color: $Paleorange;
  border-radius: 20px;
  cursor: pointer;
  @include breack-point-up(medium) {
    position: absolute;
    top: 235px;
    left: -20px;
  }
}
.next {
  height: 40px;
  width: 40px;
  padding: 13px;
  background-color: $Paleorange;
  border-radius: 20px;
  cursor: pointer;
  @include breack-point-up(medium) {
    position: absolute;
    top: 235px;
    left: 530px;
  }
}
.info {
  padding: 0 25px 88px 25px;
}
.text {
  h2 {
    color: $Orange;
    font-size: 10px;
    margin-bottom: 20px;
    @include breack-point-up(medium) {
      font-size: 12px;
    }
  }
  h1 {
    font-size: 21px;
    margin-bottom: 28px;
    @include breack-point-up(medium) {
      font-size: 35px;
    }
  }
  p {
    font-size: 12px;
    color: $Darkgrayishblue;
    margin-bottom: 38px;
    @include breack-point-up(medium) {
      font-size: 17px;
    }
  }
}
.price {
  display: flex;
  @include breack-point-up(medium) {
    display: block;
  }
  justify-content: space-between;
  align-items: center;
  h1 {
    font-size: 24px;
    margin-right: 24px;
  }
  h2 {
    font-size: 14px;
    color: $Orange;
    background-color: $Paleorange;
    padding: 6px;
    border-radius: 6px;
  }
  h3 {
    font-size: 14px;
    color: $Grayishblue;
    text-decoration: line-through;
    @include breack-point-up(medium) {
      margin-top: 15px;
    }
  }
}

.buttons {
  display: flex;
  justify-content: space-between;
  padding: 23px;
  background-color: $Lightgrayishblue;
  border-radius: 6px;
  align-items: center;
  @include breack-point-up(medium) {
    width: 155px;
  }
  img {
    width: 14px;
    height: 4px;
    cursor: pointer;
  }
  :last-child {
    height: 14px;
  }
}
.main_BTN {
  width: 100%;
  height: 56px;
  background-color: $Orange;
  border: none;
  color: white;
  border-radius: 9px;
  cursor: pointer;
}
.check_cont {
  position: absolute;
  z-index: 2;
  background-color: white;
  width: 90vw;
  left: 50%;
  top: 10px;
  transform: translateX(-50%);
  border-radius: 10px;
  padding: 25px;
  @include breack-point-up(medium) {
    width: 360px;
    top: -100px;
    right: 0;
  }
  h1 {
    font-size: 12px;
    margin-bottom: 50px;
  }
}
.check_info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
}
.check_picture {
  width: 50px;
  height: 50px;
  margin-right: 16px;
}
.delete_picture {
  height: 16px;
  width: 14px;
}
.small_pic_cont {
  :not(:last-child) {
    margin-right: 25px;
  }
}
.small_pic {
  width: 92px;
  border-radius: 5px;
  cursor: pointer;
}
.active {
  border: 2px $Orange solid;
  opacity: 50%;
}

.prev_cont {
  position: absolute;
  z-index: 3;
  background: rgba(0, 0, 0, 0.75);
  width: 100vw;
  height: 125vh;
  top: 0;
  left: 0;
}
.prev {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
}
.prev_banner {
  width: 550px;
  border-radius: 12px;
}
.relativ {
  position: relative;
}
.buttonss {
  position: absolute;
}
.close {
  position: absolute;
  color: $Orange;
  left: 520px;
  top: -50px;
  width: 30px;
  height: 30px;
  cursor: pointer;
}
