@import "./mixins.scss";
.header_cont {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  padding: 24px;
  @include breack-point-down(small) {
    overflow: hidden;
  }
  @include breack-point-up(medium) {
    padding: 0px 165px;
  }
}
.display_none_medium {
  @include breack-point-up(medium) {
    display: none !important;
  }
}

.display_none_small {
  @include breack-point-down(small) {
    display: none !important;
  }
}
.ham {
  width: 14px;
  height: 14px;
  cursor: pointer;
}
.header_links {
  @include breack-point-up(medium) {
    display: flex;
    justify-content: space-between;
    :not(:last-child) {
      margin-right: 33px;
    }
  }
  a {
    @include breack-point-up(medium) {
      text-decoration: none;
      color: $Grayishblue;
      font-size: 12px;
    }
  }
  img {
    height: 20px;
  }
}
.header_avatar {
  img {
    @include breack-point-up(medium) {
      height: 50px;
    }

    height: 25px;
  }
  & :first-child {
    margin-right: 20px;
  }
}
.menu {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: $VerydarkBlue;
  z-index: 2;
  top: 0;
  left: 0;
}
.menu_cont2 {
  width: 50vw;
  background-color: white;
  height: 100vh;
  padding: 25px;
  display: flex;
  flex-direction: column;
  img {
    height: 14px;
    width: 14px;
    margin-bottom: 55px;
    cursor: pointer;
  }
  a {
    text-decoration: none;
    color: black;
    font-size: 14px;
    margin-bottom: 30px;
  }
}

.bucket {
  position: relative;
  p {
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    right: 50%;
    position: absolute;
    width: 20px;
    height: 17px;
    background-color: $Orange;
    color: white;
    padding: 4px;
    border-radius: 10px;
    font-size: 14px;
  }
}
